.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.Sidebar, .Topbar {
  width: 340px;
  height: 100%;
  overflow: hidden;
  padding: 10px 0px 16px;
  list-style: none;
  background-color: #203d40;
  font-size: 17px;
  margin: 0;
}

.Sidebar ul {
  padding: 0;
  color: white;
}

.Sidebar ul li {
  list-style: none;
  padding: 20px 0;
  border-radius: 10px;
  -webkit-box-shadow: 0 0 10px #23d423;
          box-shadow: 0 0 10px #23d423;
  margin: 10px;
}

.Sidebar ul li:hover {
  -webkit-box-shadow: 0 0 30px #60ff60;
          box-shadow: 0 0 30px #60ff60;
  color: #60ff60;
}

.Topbar h2 {
  color: #23d423;
}

#map-container {
  height: 100%;
  position: fixed;
  right: 0;
  top: 0;
  width: calc(100% - 340px);
}

#map {
  position: relative;
  overflow: hidden;
  height: 100%;
  width: 100%;
}

@media screen and (max-width: 799px) {
  .App {
    grid-template-columns: 1fr;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
  }

  .Sidebar, .Topbar {
    width: 100%;
  }

  .Topbar {
    -ms-flex-order: 1;
        order: 1;
  }

  .Sidebar {
    -ms-flex-order: 3;
        order: 3;
  }

  #map-container {
    -ms-flex-order: 2;
        order: 2;
    width: 100vw;
    height: 300px;
    position: relative;
  }
}


